.partners {
    .container {
        .img-container {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            gap: 2rem;
            .img-wrapper {
                width: 150px;
                height: 70px;
                text-align: center;
                @media (min-width: 62rem) {
                    & {
                        width: 250px;
                        height: 90px;
                    }
                }
                img {
                    height: 100%;
                }
            }
        }
    }
}